import React from "react";
import { CardColumns } from "react-bootstrap";
import Loading from "../base_components/Loading";

const CodeCardContainer = () => {
  const InstallCard = React.lazy(() => import("./cards/InstallCard"));
  const RunningTestsCard = React.lazy(() => import("./cards/RunningTestsCard"));
  const SimpleTestCard = React.lazy(() => import("./cards/SimpleTestCard"));
  const BrowserSupportCard = React.lazy(() =>
    import("./cards/BrowserSupportCard")
  );
  const FixtureCard = React.lazy(() => import("./cards/FixtureCard"));
  const ClickActionCard = React.lazy(() => import("./cards/ClickActionCard"));
  const DragActionCard = React.lazy(() => import("./cards/DragActionCard"));
  const HoverActionCard = React.lazy(() => import("./cards/HoverActionCard"));

  return (
    <CardColumns>
      <Loading component={<InstallCard />} spinnerAnimation="grow" />
      <Loading component={<RunningTestsCard />} spinnerAnimation="grow" />
      <Loading component={<SimpleTestCard />} spinnerAnimation="grow" />
      <Loading component={<BrowserSupportCard />} spinnerAnimation="grow" />
      <Loading component={<FixtureCard />} spinnerAnimation="grow" />
      <Loading component={<ClickActionCard />} spinnerAnimation="grow" />
      <Loading component={<DragActionCard />} spinnerAnimation="grow" />
      <Loading component={<HoverActionCard />} spinnerAnimation="grow" />
    </CardColumns>
  );
};

export default CodeCardContainer;
